<template>
    <div class="body-bg">
        <div class="form-item">
            <van-search
                v-model="barcodeNumber"
                placeholder="请输入收料通知单"
                show-action
                shape="round"
                clear
                @search="dealBarcodeNumber"
                style="margin-right:1rem;margin-left:0.1rem"
                right-icon
                @keyup.enter="dealBarcodeNumber"
            >
                <div slot="action" disabled="loading" @click="dealBarcodeNumber">搜索</div>
                <div slot="right-icon" @click="openScanner"><van-icon class="iconfont" class-prefix="icon" name="saomiao" /></div>
            </van-search>
        </div>
        <div class="form-item">
            <h2 class="form-header">收料通知单数据</h2>
            <van-collapse v-model="activeDocName" accordion>
                <van-collapse-item :title="doc.fBillNo" :name="doc.id" v-for="doc in docList" :key="doc.id">
                    <van-row>
                        <van-col span="12">
                            <van-field v-model="doc.fBillNo" label="单据编码：" disabled label-align="left" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="doc.fsupplyname" label="购货单位：" disabled label-align="left" />
                        </van-col>
                    </van-row>
                    <van-collapse v-model="activeMateName" accordion>
                        <van-collapse-item :title="item.materialNumber" :name="item.id" v-for="item in doc.materialList" :key="item.id">
                            <van-row>
                                <van-col span="12">
                                    <van-field v-model="item.materialNumber" label="物料编码：" disabled label-align="left" />
                                </van-col>
                                <van-col span="12">
                                    <van-field v-model="item.materialName" label="物料名称：" disabled label-align="left" />
                                </van-col>
                            </van-row>
                            <van-row>
                                <van-col span="12">
                                    <van-field v-model="item.fQty" label="数量：" disabled label-align="left" />
                                </van-col>
                                <van-col span="12">
                                    <van-field v-model="item.materialUnit" label="单位：" disabled label-align="left" />
                                </van-col>
                            </van-row>
                            <van-row>
                                <van-col span="12">
                                    <van-field v-model="item.fstockName" label="仓库：" disabled label-align="left" />
                                </van-col>
                                <van-col span="12">
                                    <van-field v-model="item.stockPlace" label="仓位：" disabled label-align="left" />
                                </van-col>
                            </van-row>
                            <van-row>
                                <van-col span="12">
                                    <van-field v-model="item.fBatchNo" label="批号：" disabled label-align="left" />
                                </van-col>
                                <van-col span="12">
                                    <van-field v-model="item.zbm" label="自编码：" disabled label-align="left" />
                                </van-col>
                            </van-row>
                            <van-row>
                                <van-col span="6"></van-col>
                                <van-col span="6"></van-col>
                                <van-col span="6"></van-col>
                                <van-col span="6" align="center">
                                    <van-button class="button" size="mini" type="danger" @click="deleteMaterial">删除</van-button>
                                </van-col>
                            </van-row>
                        </van-collapse-item>
                    </van-collapse>
                    <van-row>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6" align="center">
                            <van-button
                                class="button"
                                size="mini"
                                type="danger"
                                @click="deleteConsignment"
                                v-if="activeMateName == '' || doc.materialList.length == 0"
                                >删除</van-button
                            >
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div class="form-item">
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="onBack">返回</van-button>
                </van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" type="danger" @click="onSubmit">确定</van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            barcodeNumber: '',
            loading: false,
            activeDocName: '',
            activeMateName: '',
            docList: []
        };
    },
    methods: {
        toastLog(data) {
            Toast.loading({
                duration: 0,
                message: data,
                forbidClick: true
            });
        },
        dealBarcodeNumber() {
            if (this.barcodeNumber.length == 0) {
                Toast.fail('发货单号不得为空！');
                return;
            }
            this.toastLog('正在获取数据...');
            this.$api.get('/selectReceiptNotice', { billNo: this.barcodeNumber }, response => {
                if (response.status >= 200 && response.status < 300) {
                    Toast.clear();
                    if (response.data.success == 'true') {
                        var json = JSON.parse(response.data.msg);
                        console.log(json);
                        var flag = true;
                        this.activeDocName = 1;
                        this.docList.some((item, i) => {
                            if (item.purchasingUnit != json.purchasingUnit) {
                                Toast.fail('购货单位必须一致！');
                                flag = false;
                            }
                        });
                        if (flag) {
                            this.$set(json, 'id', this.docList.length + 1);
                            var j = 0;
                            this.docList.some((item, i) => {
                                j = item.materialList.length + i;
                            });
                            json.materialList.some((item, i) => {
                                j = j + 1;
                                this.$set(item, 'id', j);
                            });
                            this.docList.push(json);
                            Toast.success('成功');
                            console.log(this.docList);
                        }
                    } else {
                        Toast.clear();
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.clear();
                    Toast.fail('数据获取异常！');
                    return;
                }
            });

            // var item=[{id:1,materialBarcode:'BZGJH',materialNumber:'123456',materialName:'托盘',materialBatch:'10000',stockName:'仓库',stockPlace:'仓位',materialSum:123,materialUnitId:1,materialUnit:'只',materialPrice:1},
            // {id:2,materialBarcode:'BZGJH',materialNumber:'123456',materialName:'托盘',materialBatch:'10000',stockName:'仓库',stockPlace:'仓位',materialSum:123,materialUnitId:1,materialUnit:'只',materialPrice:1}];
            // this.docList=[{id:1,type:'销售出库类型',typeId:1,docNumber:120000,salesWay:'分期收款销售',salesWayId:1,purchasingUnit:'丹阳',purchasingUnitId:1,
            // dept:'销售部',deptId:1,director:'王五',directorId:1,salesMan:'赵六',salesManId:1,materialList:item}];
        },
        onBack() {
            android.back();
        },
        onSubmit() {
            if (this.docList.length == 0) {
                Toast.fail('收料通知单数据不得为空！');
                return;
            }
            this.$router.push({ path: '/PurchaseReceipt', query: { data: this.docList } });
        },
        deleteMaterial() {
            this.docList.some((item, i) => {
                if (item.id == this.activeDocName) {
                    item.materialList.some((mate, j) => {
                        if (mate.id == this.activeMateName) {
                            if (item.materialList.length == 1) {
                                this.deleteConsignment();
                            } else {
                                item.materialList.splice(j, 1);
                            }
                            return true;
                        }
                    });
                }
            });
        },
        deleteConsignment() {
            this.docList.some((item, i) => {
                if (item.id == this.activeDocName) {
                    this.docList.splice(i, 1);
                    return true;
                }
            });
        },
        openScanner() {
            android.openScanner();
        },
        scannerSearch(data) {
            this.barcodeNumber = data;
            this.dealBarcodeNumber();
        }
    }
};
</script>


